define('todomvc/components/todo-item', ['exports'], function (exports) {
	'use strict';

	Object.defineProperty(exports, "__esModule", {
		value: true
	});
	exports.default = Ember.Component.extend({
		repo: Ember.inject.service(),
		tagName: 'li',
		editing: false,
		classNameBindings: ['todo.completed', 'editing'],
		// regexForCostString: '(/^(\d*\.)?\d+g\/(\d*\.)?\d+$/g)',

		costPerKg: Ember.computed('todo.title', function () {
			const regexForCostString = /^(\d*\.)?\d+g\/(\d*\.)?\d+$/g;
			const getRegexMatch = this.get('todo.title').match(regexForCostString);
			if (getRegexMatch) {
				const extractedData = getRegexMatch[0].split('g/');
				const costPer100g = parseInt(extractedData[1] / extractedData[0] * 100);
				return `â‚¹ ${costPer100g.toFixed(2)}/100g`;
			}
			return;
		}),

		actions: {
			startEditing() {
				this.onStartEdit();
				this.set('editing', true);
				Ember.run.scheduleOnce('afterRender', this, 'focusInput');
			},

			doneEditing(todoTitle) {
				if (!this.editing) {
					return;
				}
				if (Ember.isBlank(todoTitle)) {
					this.send('removeTodo');
				} else {
					this.set('todo.title', todoTitle.trim());
					this.set('editing', false);
					this.onEndEdit();
				}
			},

			handleKeydown(e) {
				if (e.keyCode === 13) {
					e.target.blur();
				} else if (e.keyCode === 27) {
					this.set('editing', false);
				}
			},

			toggleCompleted(e) {
				let todo = this.todo;
				Ember.set(todo, 'completed', e.target.checked);
				this.repo.persist();
			},

			removeTodo() {
				this.repo.delete(this.todo);
			}
		},

		focusInput() {
			this.element.querySelector('input.edit').focus();
		}
	});
});